import React from 'react';

class Gift extends React.Component {
  constructor() {
    super();
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e) {
    const gift = this.props.details;
    const updatedGift = {...gift,
      [e.target.name]: e.target.value};
    this.props.updateGift(updatedGift, this.props.id);
   }

  render() {
    const removeButton = <button className="remove-person-button" onClick={() => this.props.removeGift(this.props.id)}>&times;</button>
    return (
      <li className="menu-fish">
        <form className='fish-edit'>
        <input type='text' name='name' placeholder='Gift' value={this.props.details.name} onChange={(e) => this.handleChange(e)} />
        <input type='text' name='store' placeholder='Store' value={this.props.details.store} onChange={(e) => this.handleChange(e)}/>
        <select name='purchased' placeholder='Purchased?' value={this.props.details.purchased} onChange={(e) => this.handleChange(e)}>
          <option value='not purchased'>Not Purchased</option>
          <option value='purchased'>Purchased</option>
        </select>
        {removeButton}
      </form>
      </li>
    )
  }
}

export default Gift;



