import React from 'react';

class Person extends React.Component {
  render() {
    //loop throuhg gifts above or below gift forms
    const removeButton = <button className="remove-person-button" onClick={() => this.props.removePerson(this.props.id)}>&times;</button>
    return (
      <div>
        <li className="fish-name person-name-hover" onClick={() => this.props.showSelectedPerson(this.props.id)}>
          {this.props.details.name}
          {removeButton}
        </li>
      </div>
    )
  }
}

export default Person;



