import React from 'react';
import AddGiftForm from './AddGiftForm';
import Gift from './Gift';

class Gifts extends React.Component {
  render() {
    if (!this.props.person){
      return <p>SELECT SOMEONE</p>
    }
    return (
      <div>
        <h4 className="selected-person">{this.props.person.name}</h4>
        <AddGiftForm addGift={this.props.addGift} />
        <ul className="list-of-fishes">
            {
              Object
                .keys(this.props.person.gifts || {})
                .map(key => <Gift key={key} id={key} details={this.props.person.gifts[key]} updateGift={this.props.updateGift} removeGift={this.props.removeGift} />)
            }
          </ul>
      </div>
    )
  }
}

export default Gifts;



