import React from 'react';
import Header from './Header';
import People from './People';
import Gifts from './Gifts';
import base from '../base';

class App extends React.Component {
  constructor() {
    super();
    this.addPerson = this.addPerson.bind(this);
    this.showSelectedPerson = this.showSelectedPerson.bind(this);
    this.removePerson = this.removePerson.bind(this);
    this.addGift = this.addGift.bind(this);
    this.updateGift = this.updateGift.bind(this);
    this.removeGift = this.removeGift.bind(this);
    this.renderLogin = this.renderLogin.bind(this);
    this.logout = this.logout.bind(this);
    this.state = {
      people: {},
      selectedPersonId: null,
      isAuthenticated: false,
    }
  }

  addPerson(person) {
    const people = {...this.state.people}
    const timestamp = Date.now();
    people[`person-${timestamp}`] = person;
    this.setState({ people: people });
    base.update(people);
  }

  removePerson(person) {
    const people = {...this.state.people}
    people[person] = null;
    this.setState({ people });
    base.update(people);
  }

  addGift(gift) {
    const people = {...this.state.people};
    const id = this.state.selectedPersonId;
    const timestamp = Date.now();
    people[id].gifts = people[id].gifts || {}
    people[id].gifts[`gift=${timestamp}`] = gift;
    this.setState({ people: people });
    base.update(people);
  }

  updateGift(updatedGift, giftId) {
    const people = {...this.state.people};
    const id = this.state.selectedPersonId;
    people[id].gifts[giftId] = updatedGift;
    this.setState({ people: people });
    base.update(people);
  }

  removeGift(giftId){
    const people = {...this.state.people};
    const id = this.state.selectedPersonId;
    delete people[id].gifts[giftId];
    this.setState({ people: people });
    base.update(people);
  }

  showSelectedPerson(id){
    this.setState({ selectedPersonId: id });
  }

  componentWillMount() {
    // this runs right before the <App> is rendered
    base.checkUser((isAuthenticated) => {
        this.setState({ isAuthenticated });
        if (isAuthenticated){
          base.listen((people = {}) => {
            this.setState({ people: people });
          })
        }
    })

    //check if there is a selected person in local storage
    const localStorageRef = localStorage.getItem(`selectedPersonId`);

    if (localStorageRef) {
      //update App component selected person id state
      this.setState({ selectedPersonId: localStorageRef });
    }
  }

  componentWillUpdate(nextProps, nextState){
    localStorage.setItem('selectedPersonId', nextState.selectedPersonId)
  }

  authenticate(email, password) {
    base.signIn(email, password)
    base.checkUser((isAuthenticated) => {
      this.setState({ isAuthenticated });
      //if (isAuthenticated){
       // base.listen((people = {}) => {
        //  this.setState({ people: people });
        //})
      //}
    })
  }

  logout() {
    this.setState({ isAuthenticated: false });
    //stop listening?
  }

   renderLogin() {
      return (
        <nav className="login">
          <h2>Christmas List</h2>
          <p>Sign in</p>
          <input type='text' placeholder='Email' ref={(input) => { this.email = input }}/>
          <input type='password' placeholder='Password' ref={(input) => { this.password = input }}/>

          <button onClick={() => this.authenticate(this.email.value, this.password.value)}>Log In</button>
        </nav>
      )
   }

  render() {
    if (!this.state.isAuthenticated) {
      return this.renderLogin()
    }
    const logout = <button onClick={this.logout}>Log Out</button>;

    return (
      <div className="catch-of-the-day">
        <div className="menu">
          <Header tagline="Christmas List"/>
          <People
            addPerson={this.addPerson}
            people={this.state.people}
            showSelectedPerson={this.showSelectedPerson}
            removePerson={this.removePerson}
          />
          {logout}
        </div>
        <div>
          <Header tagline="Gift List"/>
          <Gifts
            person={this.state.people[this.state.selectedPersonId]}
            addGift={this.addGift}
            updateGift={this.updateGift}
            removeGift={this.removeGift}
            />
        </div>
      </div>
    )
  }
}

export default App;



