import React from 'react';

class AddGiftForm extends React.Component {
  createGift(event) {
    event.preventDefault();
    const gift = {
      name: this.name.value,
      store: this.store.value,
      purchased: this.purchased.value
    }
    this.props.addGift(gift);
    this.giftForm.reset();
  }

  render() {
    return (
      <div className='add-gift-form'>
        <form ref={(input) => this.giftForm = input} className='fish-edit' onSubmit={this.createGift.bind(this)}>
          <input ref={(input) => this.name = input} type='text' placeholder='Gift' />
          <input ref={(input) => this.store = input} type='text' placeholder='Store' />
          <select ref={(input) => this.purchased = input} placeholder='Purchased?'>
            <option value='not purchased'>Not Purchased</option>
            <option value='purchased'>Purchased</option>
          </select>
          <button type='submit'>Add Gift</button>
        </form>
      </div>
    )
  }
}


export default AddGiftForm;



