import React from 'react';
import AddPersonForm from './AddPersonForm';
import Person from './Person';

class People extends React.Component {
  render() {
    //loop through people above or below people form
    return (
      <div>
         <AddPersonForm addPerson={this.props.addPerson} />
          <ul className="list-of-names">
            {
              Object
                .keys(this.props.people)
                .map(key => <Person key={key} id={key} details={this.props.people[key]} showSelectedPerson={this.props.showSelectedPerson} removePerson={this.props.removePerson} />)
            }
          </ul>
      </div>
    )
  }
}

export default People;



